.docufile {
    &-grid {
        box-sizing: border-box;
        display: grid;
        gap: var(--sp-2);
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));

        .docufile-btn {
            // padding: var(--sp-1);
            border-color: transparent;
            display: flex;
            flex-direction: column;
            align-items: center;

            &:hover {
                background-color: var(--blue-50);
            }

            .icon {
                width: 1.75rem;
                height: 1.75rem;
            }

            .label {
                max-width: 200px;
                width: 100%;
                word-wrap: break-word;
            }

            .size {
                color: var(--gray-600);
            }
        }
    }
}
